<template>
  <div class="register-container">
    <div class="register-wrapper">
      <div id="register">
        <p class="title"></p>
        <img class="close" src="../assets/close.svg" v-on:click="closeRegister">
        <el-form
            :model="ruleForm2"
            status-icon
            :rules="rules2"
            ref="ruleForm2"
            label-width="0"
            class="demo-ruleForm"
        >
          <el-form-item prop="name">
            <el-input v-model="ruleForm2.name" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="tel">
            <el-input v-model="ruleForm2.tel" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="smscode" class="code">
            <el-input v-model="ruleForm2.smscode" placeholder=""></el-input>
            <el-button type="primary" :disabled='isDisabled' @click="sendCode">{{buttonText}}</el-button>
          </el-form-item>
          <el-form-item prop="school">
            <el-select v-model="ruleForm2.school" placeholder="" style="width: 100%" filterable :filter-method="dataFilter">
              <el-option v-for="item in stateArr" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')" style="width:100%;"></el-button>
            <p class="login" @click="gotoLogin"></p>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import Data from "../entity/Data"
export default {
  name: "Register",
  data() {
    // <!---->
    let checkUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    // <!---->
    let checkSchool = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    // <!---->
    let checkTel = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (!this.checkMobile(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    //  <!---->
    let checkSmscode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    // <!---->
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""))
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback()
      }
    }
    // <!---->
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      successfulRegister: false,
      stateArr: new Data().schools,
      stateArrCopy: new Data().schools,
      ruleForm2: {
        pass: "",
        checkPass: "",
        tel: "",
        smscode: "",
        name: "",
        school: ""
      },
      rules2: {
        pass: [{ validator: validatePass, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
        tel: [{ validator: checkTel, trigger: 'change' }],
        smscode: [{ validator: checkSmscode, trigger: 'change' }],
        name: [{validator: checkUsername, trigger: 'change'}],
        school: [{validator: checkSchool, trigger: 'change'}]
      },
      buttonText: '',
      isDisabled: false, // 
      flag: true
    }
  },
  methods: {
    // <!---->
    sendCode () {
      let tel = this.ruleForm2.tel
      if (this.checkMobile(tel)) {
        console.log(tel)
        let time = 60
        this.buttonText = ''
        this.isDisabled = true
        if (this.flag) {
          this.flag = false;
          let timer = setInterval(() => {
            time--;
            this.buttonText = time + ' '
            if (time === 0) {
              clearInterval(timer);
              this.buttonText = ''
              this.isDisabled = false
              this.flag = true;
            }
          }, 1000)
        }
      }
    },
    // <!---->
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.successfulRegister = true;
          let data = {
            'name': this.ruleForm2.name,
            'telephone': this.ruleForm2.tel,
            'school': this.ruleForm2.school,
            'password': this.ruleForm2.pass,
          }
          this.$emit('closeRegister', true);
          this.$axios.post('http://47.100.79.77:8080/User/insert', data).then((res)=>{
            console.log(res)
          })
        } else {
          console.log("error submit!!");
          return false;
        }
      })
    },
    // <!---->
    gotoLogin() {
      this.$emit('openLogin',true)
    },
    // 
    checkMobile(str) {
      let re = /^1\d{10}$/
      if (re.test(str)) {
        return true;
      } else {
        return false;
      }
    },
    dataFilter(val) {
      this.value = val;
      if (val) { //val
        this.stateArr = this.stateArrCopy.filter((item) => {
          if (!!~item.label.indexOf(val) || !!~item.label.toUpperCase().indexOf(val.toUpperCase())) {
            return true
          }
        })
      } else { //val
        this.stateArr = this.stateArrCopy;
      }
    },
    closeRegister() {
      this.successfulRegister = false;
      this.$emit('closeRegister', false)
    }
  }
};
</script>

